const Evento = (props) => {

    const{name, url, description} =props.info;

    let titulo = name.text;
    if(titulo.length > 100){
        tutulo=titulo.substr(0, 100);
    }

    let descripcion = description.text;
    if(descripcion){
        descripcion=descripcion.substr(0,200) + '...';
    }

    return ( 
        <a href={url} target="_blank" className="list-group-iten active text-light mb-1">
            <h3 className="mb-3">{titulo}</h3>
            <p className="mb-1">{descripcion}</p>
        </a>
    );
}
 
export default Evento;